<template>
  <div v-show="shown" class="messagebox-mask">
    <transition name="toast">
      <div v-show="shown" class="toast" :class="messageClass">
       <div class="title ">{{title}}</div>
      <div class="message" v-html="message"></div>
      <div class="btn-group">
         <div class="cancel col-5" v-show="showCancelButton" @click="handleCancel">{{cancelButtonText}}</div>
         <div class="sure col-5" @click="handleConfirm" >{{confirmButtonText}}</div>
      </div>
      </div>
    </transition>
  </div>
</template>
<style scoped lang="scss">
.messagebox-mask {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background:rgba(0,0,0,0.5);
  z-index: 1000;
}

.toast {
  position: absolute;
  color:#000;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
  width: 80%;
  padding: 8px 16px;
  border-radius: 4px;
  background: #fff;
  box-sizing: border-box;
  text-align: center;
  transition: opacity .3s;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.col-5{
  display:inline-block;
  width:50%;
  border:1px solid #ccc;
}

.toast-enter {
  opacity: 0;
  transform: translate3d(-50%, 20px, 0);
  transform-origin: -50% -50% 0;
}

.toast-enter-active {
  transition: all .5s cubic-bezier(0.25, 0.1, 0.25, 1.0);
}

.toast-leave {
  opacity: 1;
}

.toast-leave-active {
  transition: all .5s cubic-bezier(0.25, 0.1, 0.25, 1.0);
  opacity: 0;
}

.toast-message {
  display: block;
  font-size: 14px;
  text-align: middle;
  padding: 10px 20px;
}
</style>
<script>
export default {

  data() {
      return {
        shown: false,
        confirmButtonText:'确认',
        cancelButtonText:'取消',
        btnOk:null,
        btnCancel:null,

      }
    },
     methods: {  
        handleCancel: function (event) { 
          console.log('confirmEvent');
          if(this.btnOk){
            this.btnOk();
          }else{
            this.shown=false;
          }
        },
         handleConfirm: function (event) { 
          console.log('confirmEvent');
          if(this.btnOk){
            this.btnOk();
          }else{
            this.shown=false;
          }
        }  
      },
    props: {
      title:String,
      type:{
        default:"温馨提示",
        type:String},
      message:String,
      type:{
        default:"温馨提示内容",
        type:String
      },
      showConfirmButton:Boolean,
      type:{
        default:true,
        type:Boolean
      },
      showCancelButton:Boolean,
      type:{
        default:false,
        type:Boolean
      },
      confirmButtonText:String,
     type: {
        default:"确认",
        type:String
      },      
      cancelButtonText:String,
     type: {
        default:"取消",
        type:String
      },
      confirmEvent:Function,
      type:{
        type: Function,
        default: function () {
         alert('hi');
        }
      },
       
      className: [String, Array],
    },

    computed: {
      messageClass() {
        var classes = [];
        let className = this.className;

        if (className) {
          if (typeof className === 'string') {
            classes.push(className);
          }
          if (Array.isArray(className)) {
            classes = classes.concat(className);
          }
        }
        return classes.join(' ');
      }
    }

};
</script>



