'use strict';

import messageboxComponent from './messagebox.vue';

const plugin = {
  install(Vue) {
    const messageboxConstructor = Vue.extend(messageboxComponent);

    let instance = null;

    let getInstance = () => {
      if (instance) {
        return instance;
      }
      return new messageboxConstructor({
        el: document.createElement('div')
      });
    };

    // let removeDom = el => {
    //   if (el.parentNode) {
    //     el.parentNode.removeChild(el);
    //   }
    // }

    // messageboxConstructor.prototype.remove = function() {
    //   this.shown = false;
    //   instance = null;
    //   removeDom(this.$el);
    // }

    let setOptions = (options) => {
      if (typeof options === 'string') {
        options = {
          message: options
        }
      }
      return options || {};
    }

    const show = (options) => {
      instance = getInstance();
      instance.message = options.message || '';
      instance.title = options.title || '';
      instance.type = options.type || 'alert';
      instance.btnOk=options.btnOk;
      instance.btnCancel=options.btnCancel;

      switch (instance.type) {
        case 'alert':
          instance.showCancelButton=false;
          break;
        case 'confirm':
          instance.showCancelButton=true;
          break;
        default:
      };
      instance.shown = true;
      document.body.appendChild(instance.$el);
      return instance;
    }

    const alert = function(options) {
      options = setOptions(options);
      options = Object.assign({ type: 'alert' }, options);
      return show(options);
    }

    const confirm = function(options) {
      options = setOptions(options);
      options = Object.assign({ type: 'confirm' }, options);
      return show(options);
    }

    const success = function(options) {
      options = setOptions(options);
      options = Object.assign({ type: 'success' }, options);
      return show(options);
    }
    const Messagebox = {
      alert: alert,
      confirm: confirm,
      success: success
    };

    Vue.messagebox = Vue.prototype.$messagebox = Messagebox;


  }
}
export default plugin;



